.main__recipeName {
  display: flex;

  margin-top: 15px;
  margin-left: 35px;
}

.main__recipeName h3 {
  color: #fff;
  text-align: center;
  margin-top: 8px;
  margin-left: 15px;
}

.main__recipeName img {
  height: 40px;
  width: 40px;
}

.main__flexDiv {
  position: sticky;
  top: 0px;
  background-color: #000;
  display: flex;
  justify-content: space-between;
  height: 70px;
}

.main__flexDiv > h3 {
  color: #fff;
}

.main__inputLogo {
  display: flex;
  margin-top: 15px;
  width: 50%;
  margin-right: 20px;
  height: 38px;
  border-radius: 5px;
  background-color: #fff;
}
.main__inputLogo input {
  width: 80%;
  height: 30px;
  font-size: 20px;
  border: none;
  margin-top: 2px;
  padding-left: 7px;
  outline-width: 0;
}

.main__map {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
}

.icecream__image {
  transform: translate(-50%, -50%);
  margin-top: 18%;
  margin-left: 50%;
  width: 240px;
  height: 240px;
  opacity: 40%;
  filter: saturate(3);
}

